import fs from 'fs';



const links= [];
const nodes= [];

const n = 100;
const m = 100;


const queryString = window.location.search;
const urlParams = new URLSearchParams(queryString);
let project = urlParams.get("p")
let jsonString;
if (project=="Firefox"){
  jsonString = fs.readFileSync("jsons/geckodev.json", "utf-8");
}
if (project=="Chromium"){
  jsonString = fs.readFileSync("jsons/chromium.json", "utf-8");
}

const jsonData = JSON.parse(jsonString);
const total_dependencies = jsonData.source_metadata.total_dependencies
const total_depth = jsonData.source_metadata.total_depth
const dependencies_by_depth_separate = jsonData.source_metadata.dependencies_by_depth_separate
const dependencies_by_depth_aggregated = jsonData.source_metadata.dependencies_by_depth_aggregated
const country_distribution_by_depth_aggregated = jsonData.source_metadata.country_distribution_by_depth_aggregated
const comany_count_by_depth_aggregated = jsonData.source_metadata.comany_count_by_depth_aggregated

const commits_year_accumulated = jsonData.source_metadata.commits_year_accumulated
const contributors_year_accumulated = jsonData.source_metadata.contributors_year_accumulated



jsonData.nodes.forEach(obj => {nodes.push( {id: obj.id, 
                                            name:obj.name, 
                                            libio_stars: obj.libio_stars, 
                                            repo_url: obj.repo_url,
                                            libio_forks:obj.libio_forks,
                                            depth:obj.depth,
                                            direct_dependencies:obj.direct_dependencies,
                                            git_contributors_last_year:obj.git_contributors_last_year,
                                            git_contributors:obj.git_contributors

                                            })});
jsonData.links.forEach(obj => {links.push( {source: obj.source, target:obj.target, depth:obj.depth})});




export {commits_year_accumulated,contributors_year_accumulated,   nodes, links, total_dependencies, total_depth, dependencies_by_depth_separate, dependencies_by_depth_aggregated, country_distribution_by_depth_aggregated, project, comany_count_by_depth_aggregated};
